import React from 'react';
import classes from './greating.module.css'
import gitlImg from './Illistration.png'

const Greating = () => {
    return (
        <div className={classes.container}>
            <img className={classes.image} src={gitlImg} alt=""/>

            <div >
                <span className={classes.hi}>, !</span><br/>
                <span className={classes.hiText}>    ,      ,
                      ,     
            </span>

                <div>
                    <img src="" alt=""/>
                    <span className={classes.closeText}></span>
                </div>
            </div>
        </div>
    );
};

export default Greating;