import React from 'react';
import bird from './birthday-cake-2.png'
import connect from './connect.png'
import Card from "./card/card";
import clas from './activity.module.css'

const Activity = () => {
    return (
        <div className={clas.containder}>
            <Card text={'  '} tittle={' '} image={bird} />
            <Card text={'2 '} tittle={' offline'} image={connect}/>
        </div>
    );
};

export default Activity;