import React from 'react';
import classes from './cards.module.css'
import Card from "./card/card";

const Cards = () => {
    return (
        <div className={classes.container}>
            <Card day={'04'} month={'may'} mark={true} remind={'  ...'} candidat={'   Front ...'}/>
            <Card day={'04'} month={'may'} mark={false} remind={' '} candidat={'   QA Manual'}/>
            <Card day={'04'} month={'may'} mark={false} remind={' '} candidat={'   CTO'}/>
        </div>
    );
};

export default Cards;